.flag-list-section{
    display: flex;
    margin: 0;
    padding: 0;
    font-family: 'Rubik', sans-serif;
    background-color: #EEF0F4;
    color: #432000;
    user-select: none;
}

.flag-row{
    display: inline;
        display: flex;
    flex-direction: column;
    max-width: 320px;
}

.flag-row ul{
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.flag-row li{
    font-size: 20px;
    background-color: #FFFDF8;
    padding: 15px;
    border-radius: 8px;
    flex-grow: 1;
    text-align: center;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2)
}

.flag-row li:hover{
    background-color: #FFECC7;
    cursor: pointer;
}

.entered-countries-list , .submitted-comments{
    color: #432000;

    border: 0;
    padding: 15px;
    border-radius: 8px;
    font-size: 20px;
    text-align: center;
    font-family: 'Rubik', sans-serif;
    margin: 10px 0;
}


